.reviews {
  min-height: 560px;
  margin: 0 auto;
  padding: 87px 0 120px;

  @include vp-1199 {
    min-height: 480px;
    padding: 70px 0 100px;
  }

  @include vp-767 {
    min-height: 500px;
    padding: 40px 0 70px;
  }
}

.reviews__wrap {
  display: flex;
  flex-direction: column;
  max-width: 1320px;
  min-width: 800px;

  @include vp-1199 {
    max-width: 1289px;
    min-width: 768px;
  }

  @include vp-767 {
    flex-direction: column;
    max-width: 350px;
    min-width: 226px;
    padding: 0 47px;
  }
}

.reviews__wrap h2 {
  margin: 0 auto;
  padding-bottom: 40px;
  font-family: $ff-alumnisanssemibold;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;

  @include vp-1199 {
    font-size: 42px;
    line-height: 50px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 38px;
  }
}

.reviews__inner {
  display: grid;
  grid-template-columns: 80px 328px;
  gap: 20px 28px;
  width: 560px;
  min-height: 242px;
  margin: 0 auto;
  padding: 62px;
  background-color: #1c3374;
  color: $color-default-white;

  @include vp-1199 {
    width: 566px;
    padding: 62px 65px;
  }

  @include vp-767 {
    grid-template-columns: 1fr 1fr;
    gap: 20px 15px;
    width: auto;
    max-width: 450px;
    min-width: 226px;
    min-height: 300px;
    padding: 40px 25px;
  }
}

.reviews__image {
  grid-row: 1/3;

  @include vp-767 {
    grid-column: 1/2;
    grid-row: 1/2;
  }
}

.reviews__inner p {
  margin: 0;
  font-family: "Roboto Condensed Bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 30px;
}

.reviews__inner blockquote {
  grid-column: 2/-1;
  grid-row: 2/4;
  margin: 0;
  font-size: 16px;
  line-height: 22px;

  @include vp-767 {
    grid-column: 1/-1;
    text-align: center;
  }
}

