.reviews__slider .swiper-button-prev::after,
.reviews__slider .swiper-button-next::after {
  color: #afafaf;
  font-size: 20px;
  font-weight: 700;
}

.reviews__slider .swiper-button-prev,
.reviews__slider .swiper-button-next {
  position: absolute;
  top: 50px;
  left: 0;
  width: 16px;
  height: 32px;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}

